/* Fetch API */

window.addEventListener('load', loadImg);

const UNSPLASH_API = 'SEhkXwPPqBzUi5jwfi7T_zyQX0xkavE80DvZ7pAsVEg';

function loadImg() {
  const url = `https://api.unsplash.com/photos/random?query=ocean&client_id=${UNSPLASH_API}`;
  const imageDiv = document.querySelector('.bgImage');
  fetch(url)
    .then((response) => {
      return response.json();
    })
    .then((data) => {
      const image = new Image();
      image.src = data.urls.full;
      image.classList.add('bgImage');
      imageDiv.appendChild(image);
    });
}
